<template>
  <div class="template-page">{{text}}</div>
</template>

<script>
export default {
  data() {
    return {
      typePage: 0 //0 开发中，1无权限，2 无页面
    };
  },
  computed: {
    text() {
      let arr = [
        "页面开发中，敬请期待...",
        "抱歉，该页面无权限访问...",
        "抱歉，该页面打开出错了..."
      ];
      return arr[this.typePage] || arr[0];
    }
  },
  watch: {
    $route() {
      this.queryRouterType();
    }
  },
  created() {
    this.queryRouterType();
  },
  methods: {
    queryRouterType() {
      let { typePage } = this.$route.query;
      this.typePage = typePage;
    }
  }
};
</script>

<style>
.template-page {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  font-size: 30px;
}
</style>